.page {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  scroll-behavior: smooth;
  font-size: $main-font-size;
  line-height: 27px;
  color: $main-color;

  &__body {
    margin: 0;
    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    margin-bottom: 48px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $main-color;

    @include onDesktop {
      margin-bottom: 64px;
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__container {
    @extend %top-container;
  }
}
