.contact-us {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;

    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #9fadc2;
  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    text-decoration: none;
    color: #3e3e3e;

    @include hover(filter, contrast(900%)
    drop-shadow(0 0 0.9rem grey));
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
